.navbar {
  background-color: rgb(255, 212, 1);
  margin-bottom: 10px;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 20px;
}
.button-color {
  background-color: #0000c8;
}
.navWrapper {
  font-weight: 500;
}
.icon-color {
  color: black;
}

.navbar-logo a {
  color: black;
  text-decoration: none;
  font-size: 20px;
}

.navbar-menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  color: black;
}

.navbar-menu li {
  margin-left: 20px;
}

.navbar-menu li a {
  color: black;
  text-decoration: none;
}
/* banner section */
/* src/VideoBanner.css */
.video-banner {
  position: relative;
  width: 100%;
  height: 100vh; /* Full viewport height */
  overflow: hidden;
}

.video {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensures the video covers the entire banner */
  transform: translate(-50%, -50%);
}

.content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
}

h1 {
  font-size: 3rem;
  margin: 0;
}

.card {
  border: none;
}

.user-content p {
  margin-top: 5px;
  font-size: 12px;
}

.ratings i {
  color: yellow;
}
/* src/Banner.css */
.banner {
  background-color: #ffcc00;
  color: #fff;
  text-align: center;
  padding: 20px 0;
  font-size: 1.5rem;
  margin-bottom: 20px;
}
/* src/ProductList.css */
.product-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
}
/* src/ProductCard.css */
.product-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 16px;
  text-align: center;
}
.product-card {
  box-shadow: 10px 10px 5px lightblue;
}
.product-image {
  width: 100%;
  height: auto;
}

.product-name {
  font-size: 1.2rem;
  margin: 10px 0;
}

.product-price {
  font-size: 1rem;
  color: #333;
}
/* src/Section.css */
.section {
  display: flex;
  align-items: center;
  background-image: url("https://images.unsplash.com/photo-1494067329533-4385a4867cd4?q=80&w=1470&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D"); /* Set the path to your background image */
  background-size: cover;
  background-position: center;
  padding: 20px;
  color: #fff;
}

.image-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.centered-image {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}

.description {
  flex: 1;
  padding: 20px;
}

.description h2 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.description p {
  font-size: 1.2rem;
  line-height: 1.5;
}
/* advertisement section */
/* src/AdvertisementSection.css */
/* src/AdvertisementSection.css */
.advertisement-section {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: linear-gradient(to right, yellow 50%, blue 50%);
  color: #fff;
}

.product-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadeIn 2s ease-in-out;
}
/* success section */
.success-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  background-color: #f9f9f9;
}

.text-content {
  flex: 1;
  padding-right: 20px;
}

.image-content {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}

.image-content img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}

.text-content h2 {
  font-size: 2em;
  margin-bottom: 10px;
  color: #333;
}

.text-content p {
  font-size: 1em;
  line-height: 1.5;
  color: #555;
  margin-bottom: 10px;
}

/* success section end */
/* slider */
.each-slide-effect > div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  height: 800px;
}
.each-slide-effect svg {
  display: none;
}

.each-slide-effect span {
  padding: 20px;
  font-size: 20px;
  background: #efefef;
  text-align: center;
}
/* slider end */
.img-width {
  width: "100%" !important;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.product-image {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  animation: scaleIn 2s ease-in-out;
}
/* product detail */
.product-detail {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 20px;
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.image-section {
  flex: 1;
  max-width: 50%;
  margin-right: 20px;
}

.image-section img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.description-section {
  flex: 2;
}

.description-section h2 {
  font-size: 2em;
  margin-bottom: 10px;
  color: #333;
}

.description-section p {
  font-size: 1em;
  line-height: 1.5;
  color: #555;
  margin-bottom: 10px;
}

.description-section .price {
  font-size: 1.5em;
  color: #e60000;
  font-weight: bold;
}

/* product detail end */
/* Contact us  */
.contact-us-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}

.contact-content {
  display: flex;
  align-items: center;
}

.contact-image {
  width: 300px;
  height: 300px;
  margin-right: 20px;
  transition: transform 0.3s ease;
}

.contact-image:hover {
  transform: scale(1.1);
}

.contact-form {
  display: flex;
  flex-direction: column;
}

.contact-form label {
  margin-top: 10px;
}

.contact-form input,
.contact-form textarea {
  width: 300px;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.contact-form button {
  margin-top: 20px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.contact-form button:hover {
  background-color: #0056b3;
}

/* Contact us end */
