@keyframes scaleIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.description-container {
  flex: 1;
  padding: 20px;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  animation: slideIn 2s ease-in-out;
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.description-container h2,
.description-container p {
  animation: textSlideIn 2s ease-in-out, textPulse 3s infinite;
}

@keyframes textSlideIn {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes textPulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

.description-container h2 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.description-container p {
  font-size: 1.2rem;
  line-height: 1.5;
}

